import { checkParams } from "./isValid.js";
import { el } from "redom";
import Inputmask from "inputmask";
import { cardImages } from "./cards.js";
import valid from 'card-validator';

function handleInputBlur(validatorFunc) {
    return function() {
        if (this.value) {
            validatorFunc(this.value)
                ? this.classList.remove('wrong')
                : this.classList.add('wrong');
        } else {
            this.classList.remove('wrong');
        }
        checkParams();
    };
}

function createInputField(name, ownId, mask, validatorFunc) {
    const inputOptions = {
        type: "text",
        class: "form-control",
        id: ownId,
        required: true,
        onblur: handleInputBlur(validatorFunc)
    };
    const input = el('input', inputOptions);
    Inputmask(mask).mask(input);
    const label = el('label', name, {for: ownId});
    const div = el('div', {class: "form-floating mb-3 col-7"}, [input, label]);
    return div;
}

function handleCardInputBlur(validatorFunc, image) {
    return function() {
        if (this.value) {
            const isValid = validatorFunc(this.value);
            isValid ? this.classList.remove('wrong') : this.classList.add('wrong');
            const cardType = valid.number(this.value).card.type;
            image.src = isValid ? cardImages[cardType] : cardImages.defaultCard;
        } else {
            this.classList.remove('wrong');
            image.src = cardImages.defaultCard;
        }
        checkParams();
    };
}

function createCardNumberField(name, ownId, mask, validatorFunc) {
    const inputOptions = {
        type: "text",
        class: "form-control",
        id: ownId,
        required: true,
        onblur: handleCardInputBlur(validatorFunc)
    };
    const input = el('input', inputOptions);
    Inputmask(mask).mask(input);
    const label = el('label', name, {for: ownId});
    const inputDiv = el('div', {class: "form-floating col-6"}, [input, label]);
    const image = el('img', {src: cardImages.defaultCard});
    const div = el('div', {class: "d-flex flex-row gap-3 mb-3"}, [inputDiv, image]);
    return div;
}

export { createInputField, createCardNumberField };
